import React, {useEffect, useState} from 'react';
import axios from 'axios';
import GithubBaseStats from './GithubBaseStats'

const BaseStats = (props) => {

    const [following, setFollowing] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await axios(
                `https://api.github.com/users/${props.match.params.username}/following`
            );
            setFollowing(result.data);
            // console.log(result.data)
        };

        fetchData();
        // debugger;
    }, [props.match.params.username]);

    return !following
    ? <div>LOADING FOLLOWING...</div>
    : <div className="following-page">
        <h2>Followed By {props.match.params.username}</h2>
        {following.map(user => <GithubBaseStats user={user} key={user.id} />)}
    </div>

}

export default BaseStats;