import React from 'react';

const GithubBaseStats = ({pokemon}) => {

    // console.log(user)
        return (   
            <ul>
                        
                <img src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${pokemon.id}.png`} />
                {pokemon.name}
            </ul>
         );
    }
 
export default GithubBaseStats;