import React, { useState, useEffect } from 'react';
import { Link, Route } from 'react-router-dom';
import axios from 'axios';
import PokedexEntry from './PokedexEntry'
import Evolutions from './BaseStats'
import Types from './Type';



const Pokemon = (props) => {

    const [pokemon, setPokemon] = useState({});

    const stats = [
        {
            name: 'Types',
            value: pokemon.type,
            url: `/pokemon/${props.match.params.pokemonName}/types`
        },
        {
            name: 'Pokedex entry',
            value: pokemon.followers,
            url: `/pokemon/${props.match.params.pokemonName}/pokedexEntry`
        },
        {
            name: 'Evolutions',
            value: pokemon.following,
            url: `/pokemon/${props.match.params.pokemonName}/evolutions`
        }
    ];

    useEffect(() => {
        const fetchData = async () => {
            const result = await axios(
                `https://pokeapi.co/api/v2/pokemon/${props.match.params.pokemonName}`
            );
            setPokemon(result.data);
        };

        fetchData();
    }, [props.match.params.pokemonName]);


    /*
    This method is used as a mapping function. Eventually this could be factored out to its own component.
    */
    const renderStat = (stat) => {
        return (
            <li key={stat.name} className="pokemon-info__stat">
                <Link to={stat.url}>
                    <p className="pokemon-info__stat-value">{stat.value}</p>
                    <p className="pokemon-info__stat-name">{stat.name}</p>
                </Link>
            </li>
        );
    }

    return !pokemon
        // If the state doesn't have a pokemon key, it means the AJAX didn't complete yet. Simply render a LOADING indicator.
        ? <div className="pokemon-page">LOADING...</div>
        : (
            <div className="pokemon-page">
                <div className="pokemon-info">
                {/* <Link className="user-info__text" to={`/pokemon/${pokemon.name}`}> */}
                        <img className="pokemon-info__avatar" 
                        src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${pokemon.id}.png`} 
                        alt={`${pokemon.name} avatar`} />
                        <ul className="pokemon-info_list">
                            <li><h2 className="pokemon-info__title"> {pokemon.name} </h2></li>
                            <li><p className="pokemon-info__bio">No. {pokemon.id}</p></li>
                            <li><p> Weight: {pokemon.weight} </p></li>
                        </ul>
                {/* </Link> */}
                    {/* <ul className="pokemon-info__stats">
                        {stats.map(renderStat)}
                    </ul> */}
                </div>
                    <Route path={`${props.match.path}/evolutions`} component={Evolutions}/>   
                    <Route path={`${props.match.path}/pokedexEntry`} component={PokedexEntry}/> 
                    <Route path={`${props.match.path}/types`} component={Types}/>     

            </div>
        );
}

export default Pokemon;
