import React, {useEffect, useState} from 'react';
import axios from 'axios';
import GithubType from './GithubType';

const Types = (props) => {

    const [type, setType] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await axios(
                `https://pokeapi.co/api/v2/pokemon/${props.match.params.pokemonName}/type`
            );
            setType(result.data);
            // console.log(repos)
        };

        fetchData();
        // debugger;
    }, [props.match.params.pokemonName]);

    return !type
    ? <div>LOADING REPOS...</div>
    : <div className="type-page">
        <h2>{props.match.params.pokemonName}'s types </h2>
        {type.map(pokemon => <GithubType pokemon={pokemon} key={pokemon.types} />)}
    </div>

}

export default Types;